@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.container {
  padding-top: 60px;
  padding-bottom: 100px;
  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    margin-bottom: 65px;

    p {
      margin-top: 15px;
      max-width: 550px;
      text-align: center;
    }
  }

  .card-section {
    display: flex;
    flex-wrap: row wrap;
    justify-content: center;
    gap: 20px 180px;
  }

  .terms-and-conditions {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    label {
      display: flex;
      align-items: center;
      gap: 0 8px;

      margin-left: 350px;
    }
    .button {
      display: inline-block;
      padding: 16px 250px;
      width: fit-content;
      background-color: v.$secondary;
      border: none;
      color: white;
      text-align: center;
      text-decoration: none;
      border-radius: 5px;

      margin-top: 7px;

      transition: v.$transition;

      &:hover {
        background-color: v.$primary;
        border: none;
        color: white;
        transform: scale(0.9);
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .container {
    .card-section {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .terms-and-conditions {
      label {
        margin: 0;
      }
      .button {
        padding: 12px 20vw;
      }
    }
  }
}
