@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.label {
  max-width: fit-content;
  .container {
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 60px 20px;

    transition: v.$transition;

    // width: fit-content;
    max-width: 300px;

    .header {
      h3 {
        color: #000;
        font-size: 25px;
        font-weight: 700;
      }
    }
    .content {
      p {
        color: #000;
        font-size: 15px;
        font-weight: 300;

        margin-top: 52px;
        margin-bottom: 59px;

        max-width: 200px;
        text-align: center;
      }
    }
    .price {
      span {
        color: v.$primary;
        font-size: 20px;
        font-weight: 500;
      }
    }

    &:hover {
      transform: scale(0.9);
      transform: translateY(-15px);

      border: 1px solid #ffaa03;
    }
  }

  input {
    display: none;
    &:checked {
      & ~ .container {
        border: 1px solid #ffaa03;
        transform: scale(1.05);
      }
    }
  }
}
